@import './header';
@import './main';
@import './proccesing_form';
@import './footer';

body {
  font-family: "SF Display", sans-serif;
  color: #fff;
  background: radial-gradient(542.82px at 36.7% 44%, #0B1621 0%, #042648 100%);
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../images/background.png');
    mix-blend-mode: overlay;
    opacity: 0.12;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  } 
}