.footer {
  margin-top: 80px;
  padding-top: 20px;
  padding-bottom: 20px;
  background: linear-gradient(180deg, #06203B 0%, #061C32 100%);

  @include md {
    margin-top: 110px;
  }
}

.footer__col {
  margin-top: 20px;

  @include md {
    margin-top: 0;
  }
}

.footer__text_warning {
  line-height: 20px;  
  font-size: 18px;
  font-weight: 300;
  color: #DCDCDC;
  text-align: center;

  @include md {
    text-align: left;
    font-size: 13px;
  }
}

.footer__limit {
    text-align: center;
    width: 100%;
    margin-top: 10px;

    @include tablet {
      width: auto;
    }
  
    @include md {
      &:first-child {
        margin-left: 80px;
        margin-top: 0;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            left: -55px;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 0;
            height: 55px;
            border: 1px solid #FFFFFF;
            box-shadow: 0px 1px 4px rgba(255, 255, 255, 0.26);
        }
    }
    }
}

.footer__col_limits {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;

  @include md {
    justify-content: space-between;
  }
}

.footer__text_limit-title {
  line-height: 34px;
  font-weight: 100;
  font-size: 26px;
  color: #33BBF9;
  text-shadow: 0px 1px 4px rgba(255, 255, 255, 0.66);
}

.footer__text_limit-subtitle {
  margin-top: 5px;
  line-height: 23px;
  font-size: 17px;
  color: #FFFFFF;
  opacity: 0.57;
}
