.proccesing-form {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 15px;
  z-index: 1;
}

.proccesing-form__text_label {
  position: absolute;
  bottom: calc(100% + 5px);
  left: 0;
  line-height: 16px;
  font-size: 12px;
  color: #FFFFFF;
  opacity: 0.7;
}

.proccesing-form__input-body {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.proccesing-form__validation {
  width: 100%;
  margin-top: 5px;
  position: absolute;
  bottom: -24px;
  left: 0;
}

.proccesing-form__input {
  position: relative;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 16px;
  width: 100%;
  font-size: 18px;
  color: #07203A;
  font-family: inherit;

  &::placeholder {
    color: #07203A;
    opacity: 0.4;
  }

  @include lg {
    font-size: 22px;
  }
}

.proccesing-form__input_phone {
  padding-left: 48px;
  padding-right: 16px !important;
}

.proccesing-form__input_trans {
  border: 1px solid rgba(255, 255, 255, 0.5);
  background: none;
  border-radius: 3px;
  color: #FFFFFF;
}

.proccesing-form__currency {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  padding: 16px;
  background: #214467;
  border-radius: 3px;
  line-height: 29px;
  font-size: 22px;
  color: #FFFFFF;

}

.intl-tel-input {
  width: 100%;
}

.intl-tel-input .flag-container {
  left: 8px;
  right: inherit;
}

.proccesing-form__input-container {
  margin-top: 30px;
  position: relative;
  flex-basis: 100%;
  flex-grow: 1;
  margin-bottom: 24px;

  @include tablet {
    flex-basis: 40%;

    &:nth-child(even) {
      margin-left: 25px;
    }

  }
}

.proccesing-form__input-container_medium {
  flex-grow: 2;
}

.proccesing-form__input-container_small {
  @include tablet  {
    flex-basis: 30%;
  }
}

.proccesing-form__input-container_big {
  flex-basis: 100%;
  margin-top: 15px;
  display: flex;
}

.proccesing-form__check {
  display: none;

  &:checked+.proccesing-form__text_terms:before {
    color: #fff;
    content: "\2713";
    text-align: center;
  }
}

.proccesing-form__text_link {
  color: #33BBF9;

  &:hover,
  &:active,
  &:visited,
  &:link {
    color: #33BBF9;
  }
}

.proccesing-form__text_cancel-payment {
  position: absolute;
  bottom: 8px;
  right: 16px;
  opacity: 0.6;
  transition: opacity 0.3s;

  &:hover {
    opacity: 1;
  }
}

.proccesing-form__text_terms {
  color: #FFFFFF;
  line-height: 19px;
  font-size: 14px;
  position: relative;

  &:before {
    content: "\00a0";
    cursor: pointer;
    visibility: visible;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 2px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 16px;
    width: 16px;
    vertical-align: top;
    margin-right: 10px;
  }
}

.proccesing-form__button {
  cursor: pointer;
  margin-top: 32px;
  min-width: 175px;
  min-height: 50px;
  background: linear-gradient(180deg, #2CDCFE 0%, #3A9AF3 100%);
  border-radius: 5px;
  border: none;
  text-align: center;
  line-height: 29px;
  font-size: 22px;
  color: #FFFFFF;
  opacity: 0.7;

  &_valid {
    opacity: 1;
  }
}

.proccesing-form__text_description {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 16px;
  text-align: center;
  line-height: 16px;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.3);
  opacity: 0.82;
  width: 100%;

  @include tablet {
    font-size: 12px;
  }
}

.iti-flag {background-image: url("../images/flags.png")}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .iti-flag {background-image: url("../images/flags@2x.png");}
}
