.header_col {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 15px;
  flex-wrap: wrap;

  @include  tablet {
    padding-right: 25px;
  }
}

.header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.header__powered-by {
    margin-left: 20px;
    font-size: 9px;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    opacity: 0.5;

    @include  md {
      font-size: 15px;
    }
}

.header__text_powered-by {
    &:not(:first-child) {
        margin-right: 5px;
    }
}

.header__nav {
    list-style: none;
    align-items: center;
    margin: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 16px;

    @include tablet {
        margin-top: 0;
        width: initial;
        justify-content: initial;
    }
}

.header__nav-item {
    line-height: 20px;
    font-size: 15px;
    letter-spacing: 0.05em;
    color: #FFFFFF;

    &:not(:first-child) {
        @include  tablet {
          margin-left: 40px;
        }

        @include md {
            margin-left: 90px;
        }
    }
}

.header__nav-link {
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;

    &:active, &:link, &:hover, &:visited {
        color: #fff;
    }
}

.header__nav-link_lang {}
