.section-main {
  margin-top: 50px;

  @include md {
    margin-top: 80px;
  }
}

.section-main__row {
  position: relative;
}

.section-main__hand-bg {
  position: absolute;
  max-width: initial;
  display: none;

  @include md {
    display: block;
    width: 700px;
    top: 5%;
    height: 100%;
    right: 25%;
    z-index: 1;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../images/hand.png");
  }

  @include lg {
    width: 800px;
    top: -5%;
  }
}

.section-main__img-indacoin {
  position: absolute;
  right: 100%;
  top: 0;
  bottom: 0;
  margin: auto;
  display: none;

  @include md {
    display: block;
    width: 400px;
  }

  @include lg {
    width: auto;
  }
}

.section-main__form-container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  background: linear-gradient(180deg, #07294C 0%, #041628 100%);
  border-radius: 15px;
  padding-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 64px;

  @include tablet {
    justify-content: space-between;
    padding: 50px 60px;
  }

  @include md {
    &::after {
      content: "";
      position: absolute;
      width: 370px;
      height: 435px;
      top: 0;
      bottom: 0;
      right: -25px;
      z-index: -1;
      margin: auto;
      background: linear-gradient(180deg, #2CD9FE 0%, #3871F4 65.19%, rgba(56, 113, 244, 0.15) 98.9%);
      opacity: 0.38;
      border-radius: 15px;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      right: calc(100% - 128px);
      display: block;
      bottom: 0;
      top: 0;
      margin: auto;
      width: 540px;
      height: 580px;
      background: linear-gradient(180deg, #2CD9FE 0%, #3871F4 65.19%, rgba(56, 113, 244, 0.15) 98.9%);
      opacity: 0.8;
      border-radius: 15px;
      display: block;
    }
  }

  @include lg {
    &::before {
      right: calc(100% - 64px);
    }

    padding: 50px 90px;
  }
}

.section-main__title {
  z-index: 1;
}

.section-main__cards {
  z-index: 1;
}

.section-main__text {
  color: #FFFFFF;
}

.section-main__text_title {
  line-height: 1.3;
  font-weight: 700;
  font-size: 35px;
  letter-spacing: 0.04em;
}

.section-main__text_subtitle {
  font-size: 25px;
  font-weight: 300;
}

